import React from 'react'
import FramerText from '../hocs/FramerText'

let cached = 0

const Svg40 = ({ scenario, ...rest }) => {
  const value = scenario ? scenario.value : 0.36
  const lastValue = cached

  cached = value

  return (
    <svg width={45} height={26} {...rest}>
      <g fill="none" fillRule="evenodd">
        <rect
          width={44}
          height={25}
          x={0.5}
          y={0.5}
          fill="#FCFCF9"
          stroke="#B77941"
          rx={4.5}
        />
        <text
          scenario={scenario}
          fill="#231F20"
          fontFamily="Montserrat-SemiBold, Montserrat"
          fontSize={9}
          fontWeight={500}
          letterSpacing={-0.206}
        >
          <FramerText
            value={value}
            lastValue={lastValue}
            prefix={'\xA3'}
            order={scenario && scenario.order ? scenario.order : 1}
            x={10.657}
            y={16}
          />
        </text>
      </g>
    </svg>
  )
}

export default Svg40
